html {
  height: 100%;
}

body {
  position: relative;
  min-height: 100%;
  font-family: 'Open Sans', sans-serif;
}

.hide-screen {
  display: none;
}

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
