}

h1, .h1 {
  font-size: 2.5714285714em;
  margin-top: 0.5555555556em;
  margin-bottom: 0.2777777778em;
}

@media (max-width: 991px) {
  h1, .h1 {
    font-size: 2.0571428571em;
  }
}

h2, .h2 {
  font-size: 2.1428571429em;
  margin-top: 0.6666666667em;
  margin-bottom: 0.3333333333em;
}

@media (max-width: 991px) {
