.header-bg {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
  background: #2C72B4;
}

.header-bg .carousel,
.header-bg .carousel-inner,
.header-bg .item {
  width: 100%;
  height: 100%;
}

.header-bg .item {
  background: #2C72B4 no-repeat/cover;
}

.header-bg .carousel-fade .carousel-inner .item {
  transition-property: opacity;
}

.header-bg .carousel-fade .carousel-inner .item,
.header-bg .carousel-fade .carousel-inner .active.left,
.header-bg .carousel-fade .carousel-inner .active.right {
