  z-index: 1200;
  display: none;
}

body.esperando {
  overflow: hidden;
}

body.esperando .spinner-container {
  display: block;
}

.spinner {
  background-color: #2C72B4;
  width: 100%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  text-align: center;
  padding: 2em 0;
}

.spinner .logo-spinner {
  fill: #fff;
  width: 150px;
}

.spinner .ball {
  width: 20px;
  height: 20px;
  border-radius: 100%;
  background-color: #F1F7F8;
  animation: motion 3s cubic-bezier(0.5, 0, 0.2, 1) infinite;
  margin: 10px auto;
}

.spinner .loading {
  margin: 10px auto;
}

.spinner .loading .bullet {
  display: inline-block;
  width: 12px;
  height: 12px;
  border-radius: 100%;
  background-color: #F1F7F8;
  animation: animIn 1.1s ease-in-out 0s infinite;
}

.spinner .loading .bullet:nth-child(1) {
  animation-delay: 0s;
}

.spinner .loading .bullet:nth-child(2) {
  animation-delay: 0.3s;
}

.spinner .loading .bullet:nth-child(3) {
  animation-delay: 0.6s;
}

.spinner .loading .bullet:nth-child(4) {
  animation-delay: 0.9s;
}

@keyframes motion {
  0% {
    transform: translateX(0) scale(1);
  }
  25% {
    transform: translateX(-100px) scale(0.3);
  }
  50% {
    transform: translateX(0) scale(1);
  }
  75% {
    transform: translateX(100px) scale(0.3);
  }
  100% {
    transform: translateX(0) scale(1);
  }
}

@keyframes animIn {
  0% {
    transform: translateX(-80px);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: translateX(80px);
    opacity: 0;
  }
}

